//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

// PT Sans
@font-face {
	@include fontdefault(normal, 400, 'PTSans');
	@include fontpath('PTSans-Regular', 'PTSans-Regular', PTSans-Regular); }
